"use client";

import React, { useState, useCallback } from 'react';
import * as XLSX from 'xlsx';
import { UploadCloud, Loader2 } from 'lucide-react';
import { useToast } from "@/hooks/use-toast";
import { Card, CardContent } from "@/components/ui/card";
import { Button } from './ui/button';

type ParsedData = Record<string, string | number>[];

interface FileUploaderProps {
  onDataUploaded: (data: ParsedData, headers: string[]) => void;
}

export default function FileUploader({ onDataUploaded }: FileUploaderProps) {
  const [isParsing, setIsParsing] = useState(false);
  const { toast } = useToast();
  const fileInputRef = React.useRef<HTMLInputElement>(null);

  const handleFile = useCallback((file: File) => {
    setIsParsing(true);
    const reader = new FileReader();
    reader.onload = (e) => {
      try {
        const data = e.target?.result;
        const workbook = XLSX.read(data, { type: 'array' });
        const sheetName = workbook.SheetNames[0];
        const worksheet = workbook.Sheets[sheetName];
        const json = XLSX.utils.sheet_to_json<Record<string, string | number>>(worksheet);
        
        if (json.length === 0) {
          throw new Error("The uploaded Excel file is empty or couldn't be read.");
        }
        
        const headers = Object.keys(json[0]);
        onDataUploaded(json, headers);
      } catch (error) {
        console.error(error);
        toast({
          variant: "destructive",
          title: "Parsing Error",
          description: error instanceof Error ? error.message : "Could not parse the Excel file. Please ensure it's a valid .xls or .xlsx file.",
        });
      } finally {
        setIsParsing(false);
      }
    };
    reader.onerror = (error) => {
        console.error(error);
        toast({
          variant: "destructive",
          title: "File Read Error",
          description: "There was an error reading the file.",
        });
        setIsParsing(false);
    }
    reader.readAsArrayBuffer(file);
  }, [onDataUploaded, toast]);

  const onDrop = useCallback((acceptedFiles: File[]) => {
    if (acceptedFiles.length > 0) {
      handleFile(acceptedFiles[0]);
    }
  }, [handleFile]);

  const handleDragOver = (e: React.DragEvent<HTMLDivElement>) => {
    e.preventDefault();
  };

  const handleDrop = (e: React.DragEvent<HTMLDivElement>) => {
    e.preventDefault();
    if (e.dataTransfer.files && e.dataTransfer.files.length > 0) {
      handleFile(e.dataTransfer.files[0]);
      e.dataTransfer.clearData();
    }
  };
  
  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files && e.target.files.length > 0) {
      handleFile(e.target.files[0]);
    }
  };

  const handleClick = () => {
    fileInputRef.current?.click();
  };

  return (
    <div className="flex items-center justify-center w-full">
      <Card 
        className="w-full max-w-lg border-2 border-dashed border-muted-foreground/50 hover:border-primary transition-colors duration-300"
        onDragOver={handleDragOver}
        onDrop={handleDrop}
      >
        <CardContent className="p-6 text-center">
          <input
            type="file"
            ref={fileInputRef}
            onChange={handleFileChange}
            accept=".xlsx, .xls"
            className="hidden"
            disabled={isParsing}
          />
          <UploadCloud className="mx-auto h-16 w-16 text-muted-foreground" />
          <h3 className="mt-4 text-xl font-semibold font-headline">Drop your Excel file here</h3>
          <p className="mt-1 text-sm text-muted-foreground">or click to browse</p>
          <Button onClick={handleClick} className="mt-6" disabled={isParsing}>
            {isParsing ? (
              <>
                <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                Parsing...
              </>
            ) : (
              'Browse Files'
            )}
          </Button>
          <p className="text-xs text-muted-foreground mt-4">Supports .xls and .xlsx files</p>
        </CardContent>
      </Card>
    </div>
  );
}
