"use client";

import React, { useState, useRef, useCallback, useMemo, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import html2canvas from 'html2canvas';
import { generateInsights, GenerateInsightsOutput } from '@/ai/flows/generate-insights';
import { getAuth, onAuthStateChanged, signOut } from 'firebase/auth';
import { app } from '@/lib/firebase'; // Assuming you have firebase initialized here

import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue, SelectGroup, SelectLabel } from '@/components/ui/select';
import { useToast } from '@/hooks/use-toast';
import { Loader2, Download, BarChart, LineChartIcon, PieChart, Lightbulb, LogOut } from 'lucide-react';
import ChartDisplay from './chart-display';
import AiInsights from './ai-insights';
import FileUploader from './file-uploader';

type ParsedData = Record<string, string | number>[];
export type ChartType = 'bar' | 'line' | 'pie';

export default function AnalyticsDashboard() {
  const [data, setData] = useState<ParsedData | null>(null);
  const [headers, setHeaders] = useState<string[]>([]);
  const [chartType, setChartType] = useState<ChartType>('bar');
  
  const [xAxisKey, setXAxisKey] = useState<string>('');
  const [yAxisKey, setYAxisKey] = useState<string>('');
  
  const [nameKey, setNameKey] = useState<string>('');
  const [valueKey, setValueKey] = useState<string>('');

  const [insights, setInsights] = useState<string | null>(null);
  const [isGenerating, setIsGenerating] = useState(false);
  const chartContainerRef = useRef<HTMLDivElement>(null);
  const { toast } = useToast();
  const router = useRouter();
  const auth = getAuth(app);

  useEffect(() => {
    // This check is disabled to allow direct access for demo purposes.
    // In a real application, you would re-enable this.
    // const unsubscribe = onAuthStateChanged(auth, (user) => {
    //   if (!user) {
    //     router.push('/login');
    //   }
    // });
    // return () => unsubscribe();
  }, [auth, router]);

  const handleLogout = async () => {
    try {
      // In a real app with Firebase, you would uncomment this
      // await signOut(auth);
      router.push('/login');
      toast({
        title: "Logged Out",
        description: "You have been successfully logged out.",
      });
    } catch (error) {
      console.error("Logout error:", error);
      toast({
        variant: "destructive",
        title: "Logout Error",
        description: "Could not log you out. Please try again.",
      });
    }
  };

  const handleDataUploaded = useCallback((parsedData: ParsedData, newHeaders: string[]) => {
    setData(parsedData);
    setHeaders(newHeaders);
    // Reset chart configs
    setXAxisKey('');
    setYAxisKey('');
    setNameKey('');
    setValueKey('');
    setInsights(null);
    toast({
      title: "File Uploaded Successfully",
      description: "Your data is ready for analysis.",
    });
  }, [toast]);
  
  const handleGenerateInsights = useCallback(async () => {
    if (!data) return;
    setIsGenerating(true);
    setInsights(null);
    try {
      const result = await generateInsights({ data: JSON.stringify(data) });
      setInsights(result.insights);
    } catch (error) {
      console.error(error);
      toast({
        variant: "destructive",
        title: "AI Error",
        description: "Could not generate insights. Please try again.",
      });
    } finally {
      setIsGenerating(false);
    }
  }, [data, toast]);

  const handleExport = useCallback(async () => {
    if (chartContainerRef.current) {
      try {
        const canvas = await html2canvas(chartContainerRef.current, { backgroundColor: null, useCORS: true });
        const dataUrl = canvas.toDataURL('image/png');
        const link = document.createElement('a');
        link.href = dataUrl;
        link.download = `${chartType}-chart.png`;
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        toast({
          title: "Chart Exported",
          description: "Your chart has been downloaded as a PNG.",
        });
      } catch (error) {
        console.error("Export error:", error);
        toast({
          variant: "destructive",
          title: "Export Error",
          description: "Could not export the chart.",
        });
      }
    }
  }, [chartType, toast]);
  
  const numericHeaders = useMemo(() => {
    if (!data || data.length === 0) return [];
    return headers.filter(h => typeof data[0][h] === 'number');
  }, [data, headers]);

  const chartConfig = useMemo(() => {
    if (chartType === 'pie') {
      return { nameKey, valueKey };
    }
    return { xAxisKey, yAxisKey };
  }, [chartType, xAxisKey, yAxisKey, nameKey, valueKey]);

  if (!data) {
    return (
      <div className="min-h-screen flex flex-col items-center justify-center bg-background p-4 sm:p-6 lg:p-8">
        <div className="absolute top-4 right-4">
          <Button onClick={handleLogout} variant="outline">
            <LogOut className="mr-2 h-4 w-4" />
            Logout
          </Button>
        </div>
        <div className="text-center mb-8">
            <h1 className="font-headline text-4xl font-bold text-primary sm:text-5xl">Excel Analytics Platform V</h1>
            <p className="text-muted-foreground mt-2 max-w-2xl mx-auto">Upload an Excel file to visualize data and generate AI-powered insights.</p>
        </div>
        <FileUploader onDataUploaded={handleDataUploaded} />
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-background p-4 sm:p-6 lg:p-8">
      <header className="mb-8 flex justify-between items-center">
        <div>
          <h1 className="font-headline text-4xl font-bold text-primary">Analytics Dashboard</h1>
          <p className="text-muted-foreground mt-2">Visualize your data and discover new insights.</p>
        </div>
        <Button onClick={handleLogout} variant="outline">
          <LogOut className="mr-2 h-4 w-4" />
          Logout
        </Button>
      </header>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        <div className="lg:col-span-1 space-y-6">
          <Card>
            <CardHeader>
              <CardTitle className="font-headline">Chart Controls</CardTitle>
              <CardDescription>Customize your data visualization.</CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              <Select value={chartType} onValueChange={(v) => setChartType(v as ChartType)}>
                <SelectTrigger><SelectValue placeholder="Select chart type" /></SelectTrigger>
                <SelectContent>
                  <SelectItem value="bar"><div className='flex items-center gap-2'><BarChart className="h-4 w-4"/>Bar Chart</div></SelectItem>
                  <SelectItem value="line"><div className='flex items-center gap-2'><LineChartIcon className="h-4 w-4"/>Line Chart</div></SelectItem>
                  <SelectItem value="pie"><div className='flex items-center gap-2'><PieChart className="h-4 w-4"/>Pie Chart</div></SelectItem>
                </SelectContent>
              </Select>
              
              {chartType === 'pie' ? (
                <>
                  <Select value={nameKey} onValueChange={setNameKey}>
                    <SelectTrigger><SelectValue placeholder="Select Name Field" /></SelectTrigger>
                    <SelectContent>{headers.map(h => <SelectItem key={h} value={h}>{h}</SelectItem>)}</SelectContent>
                  </Select>
                  <Select value={valueKey} onValueChange={setValueKey}>
                    <SelectTrigger><SelectValue placeholder="Select Value Field" /></SelectTrigger>
                    <SelectContent>{numericHeaders.map(h => <SelectItem key={h} value={h}>{h}</SelectItem>)}</SelectContent>
                  </Select>
                </>
              ) : (
                 <>
                  <Select value={xAxisKey} onValueChange={setXAxisKey}>
                    <SelectTrigger><SelectValue placeholder="Select X-Axis" /></SelectTrigger>
                    <SelectContent>{headers.map(h => <SelectItem key={h} value={h}>{h}</SelectItem>)}</SelectContent>
                  </Select>
                  <Select value={yAxisKey} onValueChange={setYAxisKey}>
                    <SelectTrigger><SelectValue placeholder="Select Y-Axis" /></SelectTrigger>
                    <SelectContent>{numericHeaders.map(h => <SelectItem key={h} value={h}>{h}</SelectItem>)}</SelectContent>
                  </Select>
                 </>
              )}
            </CardContent>
          </Card>
          
          <Card>
            <CardHeader>
                <CardTitle className="font-headline">Actions</CardTitle>
            </CardHeader>
            <CardContent className="flex flex-col space-y-3">
              <Button onClick={handleGenerateInsights} disabled={isGenerating}>
                {isGenerating ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <Lightbulb className="mr-2 h-4 w-4" />}
                Generate Insights
              </Button>
              <Button onClick={handleExport} variant="secondary">
                <Download className="mr-2 h-4 w-4" />
                Export Chart as PNG
              </Button>
            </CardContent>
          </Card>
        </div>

        <div className="lg:col-span-2 space-y-6">
            <Card ref={chartContainerRef}>
                <CardHeader>
                    <CardTitle className="font-headline capitalize">{chartType} Chart</CardTitle>
                    <CardDescription>Visual representation of your selected data.</CardDescription>
                </CardHeader>
                <ChartDisplay chartType={chartType} data={data} config={chartConfig} />
            </Card>
            <AiInsights insights={insights} isLoading={isGenerating} />
        </div>
      </div>
    </div>
  );
}
